.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.container {
  background-color: white;
  max-width: 650px;
  min-width: 310px;
  padding: 25px;
  padding-top: 15px;
  padding-bottom: 25px;
  margin: 10px;
  color: #282c34;
  border-radius: 10px;
}

.btns {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 20px;
}

.btn{
  padding: 10px;
  border: none;
  color: white;
  background-color: #282c34;
  border-radius: 5px;
  font-size: calc(5px + 1vmin);
  text-decoration: none;
}

#linked-quote{
  margin-left: 10px;
}

span {
  font-weight: 100;
  font-size: 1rem;
  display: flex;
  justify-content: flex-end;
}

@media (max-width: 400px) {
  .App {
    min-height: 92.6vh;
  }
}